<template>
     <div class="container login-container login-background">
            <div class="row">
                <div class="col-md-6 login-form-1 border-radius-login-left">
                    <div class="row">
                        <div class="col-md-12 mt-5">
                            <h4 class="marg font-weight-bold ">Login</h4>
                            <div colspan="2" class="bold-text lineweight">
                                <hr class="line-color mb-0 mt-0">
                            </div>
                        </div>
                        <div class="col-md-12 mt-4 ">
                            <form action="">
                                <div class="form-group">
                                    <label for="email" class="font-weight-bold text-dark">Email:</label>
                                    <input type="email" class="form-control" id="email" placeholder="Your Email">
                                </div>
                                <div class="form-group">
                                    <label for="pwd" class="font-weight-bold text-dark">Password:</label>
                                    <input type="password" class="form-control" id="pwd" placeholder="Your Password">
                                </div>
                            <div class="form-group text-right">
                               <!--  <a href="forgotpassword.html" class="ForgetPwd">Forget Password?</a> -->
                                <router-link to="/forgotpassword" class="ForgetPwd">Forget Password?</router-link>
                            </div>
                                
                                <div class="form-group">
                                    <input type="submit" class="btnSubmit w-100" value="Login" />
                                </div>
                                <div class="form-group text-right ">
                                    <!-- <a href="register.html" class="ForgetPwd text-muted opacity">Don't have an account? Register</a> -->
                                    <router-link to="/register" class="ForgetPwd text-muted opacity">Don't have an account? Register</router-link>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
                <div class="col-md-6 login-form-2 border-radius-login-right">
                        <img src="@/assets/Resource/ic_login_logo.png" alt="">
                </div>
            </div>
        </div>
    
</template>

<script>
export default {

}
</script>

<style scoped>

</style>


